        <el-tab-pane label="" name="drop">
            <span slot="label">
                <i class="el-icon-present"></i>
                <b></b>
                <!-- <em class="u-new"></em> -->
            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="gem">
            <span slot="label">
                <i class="el-icon-cherry"></i>
                <b></b>
            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="cj">
            <span slot="label">
                <i class="el-icon-medal"></i>
                <b></b>
                <!-- <em class="u-new"></em> -->
            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="story">
            <span slot="label">
                <i class="el-icon-film"></i>
                <b></b>
            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="npc">
            <span slot="label">
                <i class="el-icon-aim"></i>
                <b></b>
