            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="attr">
            <span slot="label">
                <i class="el-icon-help"></i>
                <b></b>
                <!-- <em class="u-new"></em> -->
            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="skill" v-if="isSuperAuthor && client == 'std'">
            <span slot="label">
                <i class="el-icon-key"></i>
                <b></b>
                <em class="u-ready"></em>
            </span>
        </el-tab-pane>

        <el-tab-pane label="" name="lua" v-if="isEditor">
            <span slot="label">
                <i class="el-icon-full-screen"></i>
                <b></b>
                <em class="u-ready"></em>
            </span>
        </el-tab-pane>

